import { mapActions } from 'vuex'
const URL = 'https://haomiaozhichen.github.io/ebhs-review-data.json'
export default {
  name: 'App',
  data () {
    return {}
  },
  computed: {},
  created(){
    this.getWebData()
  },
  methods:{
    ...mapActions(['setStore']),
    async getWebData(){
      this.$http.get(URL).then((res) => {
        console.log('reviewTasks.json res:', res)
      })
    }
  }
}



